@charset "UTF-8";
@import url("https://fonts.googleapis.com/css?family=Nunito");
html {
  height: 100%;
  overflow-x: hidden;
}

body {
  display: flex;
  min-height: 100%;
  flex-direction: column;
  margin: 0;
  font-family: "RobotoMedium", sans-serif;
  /*скрыть дерганье страницы в появлении полосы прокрутки*/
  padding: 0 calc(0px - (100vw - 100%)) 0 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  overflow-x: hidden;
}
@media (max-width: 500px) {
  body {
    padding: 0;
  }
}

p {
  letter-spacing: 1px;
}

a {
  text-decoration: none;
}

ul, li {
  list-style: none;
  margin: 0;
  padding: 0;
}

input,
textarea,
textarea:focus,
.button:focus {
  outline: none;
}

.flex1 {
  display: flex !important;
}

.none {
  display: none;
}

.logo {
  display: flex;
  margin: 0 0 0 10px;
  align-items: center;
  height: 30px;
  flex: 1 0 auto;
}
.logo a {
  width: 100px;
  display: block;
}

.logo_square {
  height: 100%;
  margin-right: 5px;
  width: 30px;
}
@media (max-width: 800px) {
  .logo_square {
    display: none;
  }
}
@media (max-width: 600px) {
  .logo_square {
    display: none;
  }
}

.header .logo_vitex {
  height: 30px;
}
@media (max-width: 600px) {
  .header .logo_vitex {
    height: 25px;
  }
}

.logo__description {
  width: 120px;
  margin: 0 10px 0 20px;
  display: inline-block;
  color: #343133;
  font-size: 12px;
}
@media (max-width: 800px) {
  .logo__description {
    display: none;
    font-size: 10px;
    width: 90px;
  }
}
@media (max-width: 400px) {
  .logo__description {
    display: none;
  }
}

.location {
  font-size: 12px;
  padding: 0 15px;
  max-width: 105px;
  position: relative;
  flex: 1;
}
@media (max-width: 800px) {
  .location {
    display: none;
  }
}
.location svg {
  position: absolute;
  left: -8px;
  top: 30%;
  width: 15px;
  height: 15px;
  stroke: var(--color-main);
}

/***** Всплывающие телефоны */
.phone {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  font: bold 17px/21px "Proxima Nova", sans-serif;
  padding: 0 25px;
  white-space: nowrap;
  flex: 1;
}
@media (max-width: 500px) {
  .phone {
    font: bold 12px/17px "Proxima Nova", sans-serif;
  }
}

.phone svg {
  position: absolute;
  left: -20px;
  bottom: 3px;
  color: #1bbc9b;
  width: 14px;
}
@media (max-width: 400px) {
  .phone svg {
    font-size: 17px;
    width: 11px;
    bottom: 2px;
    left: -18px;
  }
}

.phone > a {
  position: relative;
  color: #4e4e4e;
}
.phone > a:after {
  content: "";
  position: absolute;
  left: -25px;
  top: 30%;
  display: inline-block;
  border: 5px solid transparent;
  border-top-color: var(--color-main);
  margin-left: 9px;
}
@media (max-width: 500px) {
  .phone > a:after {
    position: absolute;
    top: 5px;
  }
}

.phone:hover .phone__popup-inner {
  padding: 25px 30px;
  height: auto;
  overflow: visible;
}

.phone__popup {
  position: absolute;
  left: -15%;
  bottom: -10px;
  padding-top: 0;
  height: 0;
  z-index: 6;
}
@media (max-width: 600px) {
  .phone__popup {
    right: 0;
    left: initial;
  }
}

.phone__popup-inner {
  width: 200px;
  background: #fff;
  box-shadow: 0 6px 12px #8fa39f;
  font: 15px/19px "Proxima Nova", sans-serif;
  color: #181617;
  padding: 0 30px;
  height: 0;
  overflow: hidden;
  border-radius: 2px;
  position: relative;
  transition: all 0.3s;
}

.phone__popup-inner:before {
  content: "";
  border-bottom: 10px solid #fff;
  border-left: 11px solid transparent;
  border-right: 11px solid transparent;
  position: absolute;
  left: 192px;
  top: -10px;
}

.phone__popup-head {
  border-bottom: 1px solid #ebebeb;
  padding-bottom: 15px;
  margin-bottom: 16px;
  white-space: initial;
}

.phone__popup p {
  margin: 0 0 11px;
}

.phone__popup-phones {
  margin: 0;
}

.phone__popup-phones a {
  font: bold 17px/21px "Proxima Nova", sans-serif;
  color: #343133;
  line-height: 1.7;
}

.header-catalog-menu {
  position: sticky;
  top: 1px;
  background: var(--color-main);
  min-height: 40px;
  display: flex;
  opacity: 0.92;
  z-index: 1;
}

.header-catalog-menu__wrap {
  max-width: 1310px;
  width: 100%;
  margin: 0 auto;
  display: flex;
  justify-content: flex-end;
}
.header-catalog-menu__wrap .mobile-menu {
  position: absolute;
  top: 100%;
  right: -150%;
  width: 250px;
  background: #78aafe;
  transition: right 500ms ease-out;
}
@media (max-width: 500px) {
  .header-catalog-menu__wrap .mobile-menu {
    width: 100%;
  }
}
.header-catalog-menu__wrap .mobile-menu.show {
  right: 0;
}
.header-catalog-menu__wrap .mobile-menu .mobile-menu__wrap {
  display: flex;
  flex-flow: column;
  margin: 20px;
  text-align: end;
}
@media (max-width: 500px) {
  .header-catalog-menu__wrap .mobile-menu .mobile-menu__wrap {
    text-align: center;
  }
}
.header-catalog-menu__wrap .mobile-menu .mobile-menu__wrap .mobile-menu__item {
  color: #ffffff;
  padding: 10px 50px 10px;
  cursor: pointer;
}
.header-catalog-menu__wrap .mobile-menu .mobile-menu__wrap .mobile-menu__item:hover {
  background-color: #0070ff;
}

.utils {
  display: flex;
  padding: 0 20px;
  align-items: center;
  justify-content: space-between;
}
.utils svg {
  stroke: white;
  stroke-width: 3px;
  width: 17px;
  height: 17px;
  padding: 0 15px;
}
.utils .search,
.utils .gamburger,
.utils .cart {
  position: initial;
  height: 100%;
  margin: initial;
  display: flex;
  cursor: pointer;
  align-items: center;
}
.utils .search:hover,
.utils .gamburger:hover,
.utils .cart:hover {
  background-color: #0070ff;
}
.h-cat {
  background-color: rgba(34, 117, 255, 0);
  position: relative;
  cursor: pointer;
  text-transform: uppercase;
  transition: 0.5s;
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 1;
  color: #fff;
  font: 600 17px/17px intro, "Proxima Nova", sans-serif;
}
@media (max-width: 800px) {
  .h-cat {
    display: none;
  }
}
.h-cat:first-child ul {
  left: 0;
}
.h-cat:first-child ul:before {
  right: 80%;
}
.h-cat:last-child ul {
  right: 0;
}
.h-cat:hover {
  background-color: rgba(34, 117, 255, 0.5);
}
.h-cat:hover ul {
  height: auto;
  padding: 30px 25px 15px 25px;
  max-height: fit-content;
  background-color: white;
  top: 100%;
  box-shadow: 0 10px 25px 0 rgba(0, 0, 0, 0.28);
  overflow: visible;
  cursor: text;
}
.h-cat:hover ul li {
  cursor: pointer;
  padding: 10px 5px;
  border-bottom: #f0f0f0 1px solid;
  transition: 0.3s;
}
.h-cat:hover ul li:hover {
  background: #ababab33;
}
.h-cat:hover ul li a:hover {
  background: #eeeeee;
}
.h-cat ul {
  height: 0;
  position: absolute;
  top: 45px;
  overflow: hidden;
  color: black;
  z-index: 2;
}
.h-cat ul li a {
  font: 600 14px/17px "Proxima Nova", sans-serif;
}
.h-cat ul:before {
  content: "";
  display: block;
  width: 0;
  height: 0;
  transform: rotate(45deg);
  position: absolute;
  top: -4px;
  right: 50%;
  border-top: 10px solid white;
  border-left: 10px solid white;
}

.user-menu {
  position: relative;
  display: flex;
  align-items: center;
  font-size: 17px;
  cursor: pointer;
  color: #2b303a;
  margin-right: 10px;
  flex: 0 1 auto;
}
@media (max-width: 500px) {
  .user-menu {
    font-size: 12px;
    display: none;
    padding: 3px 10px;
  }
}
.user-menu:hover {
  background-color: #f0f0f0;
}
.user-menu img {
  border: 2px solid #f5f5f5;
  border-radius: 50%;
  vertical-align: middle;
  margin-right: 6px;
  width: 44px;
}
.user-menu .user-menu__menu {
  display: none;
  flex-flow: column;
  border: 1px solid #eee;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);
  background: #fff;
  z-index: 501;
  position: absolute;
  top: 100%;
  right: -1px;
  width: 280px;
  padding: 10px;
}
.user-menu .user-menu__menu a {
  padding: 5px 10px;
  color: #5a5a5a;
}
.user-menu .user-menu__menu a:hover {
  background-color: #e5e5e5;
}
.user-menu:hover > .user-menu__menu {
  display: flex;
}

.guest-menu {
  position: relative;
  display: flex;
  align-items: center;
  font-size: 17px;
  cursor: pointer;
  color: #2b303a;
  margin-right: 10px;
  flex: 0 1 auto;
  border: 1px solid grey;
  border-radius: 4px;
  padding: 5px 15px;
  z-index: 2;
}
@media (max-width: 500px) {
  .guest-menu {
    font-size: 12px;
    display: none;
    padding: 3px 10px;
  }
}
.guest-menu svg {
  width: 17px;
  padding-right: 10px;
}
.guest-menu .guest-menu__menu {
  position: absolute;
  display: none;
  flex-flow: column;
  background-color: #ffffff;
  padding: 10px;
  top: 103%;
  right: -1px;
  height: max-content;
  box-shadow: 0px 11px 16px 0px #1d7c6840;
  z-index: 1;
}
.guest-menu .guest-menu__menu a {
  color: #5a5a5a;
  padding: 5px 10px;
}
.guest-menu .guest-menu__menu a:hover {
  background-color: #e5e5e5;
}
.guest-menu .user-menu__icon {
  width: 14px;
  height: 15px;
  padding: 0 5px 3px;
}
.guest-menu:hover > .guest-menu__menu {
  display: flex;
}

header {
  display: flex;
  flex: 0 0 auto;
  background: #fff;
  width: 100%;
  align-items: center;
  justify-content: center;
}

.header {
  display: flex;
  position: relative;
  align-items: center;
  margin: 10px 20px;
  justify-content: space-between;
  max-width: 1310px;
  height: 50px;
}
@media (max-width: 600px) {
  .header {
    padding: 0;
  }
}

.header__info {
  display: flex;
  position: relative;
  align-items: center;
  max-width: 1310px;
  justify-content: space-between;
  flex-wrap: wrap;
}
@media (max-width: 600px) {
  .header__info {
    padding: 0;
  }
}

footer {
  flex-flow: column;
  flex: 0 0 auto;
  background: url("../../../pic/header-big.png") repeat;
  padding: 10px;
  min-height: 110px;
  color: #9b9b9b;
}
footer a {
  color: #ffffff;
}
footer p {
  font-size: 10px;
}

.footer_legal {
  display: flex;
  justify-content: space-between;
  gap: 100px;
}

.footer_menu {
  display: flex;
  gap: 15px;
  justify-content: center;
  max-width: 700px;
  margin: 0 auto;
  border-bottom: 1px solid #7a7a7a;
  padding-top: 30px;
  padding-bottom: 30px;
}
.footer_menu .column {
  display: flex;
  flex-flow: column;
}
@media (max-width: 500px) {
  .footer_menu .column {
    font-size: 10px;
  }
}
.footer_menu .column a {
  width: 100px;
  padding: 5px;
}

.form-container {
  display: flex;
  flex-flow: column wrap;
  padding: 35px 30px 15px 30px;
  background: #eee;
  width: clamp(50px, 66%, 300px);
  margin: 3% auto;
  align-items: stretch;
}
.form-container p {
  font-size: 0.6rem;
}

.form-title {
  font-weight: bold;
  font-size: 120%;
  text-align: center;
  color: #949494;
  margin-bottom: 20px;
}

.form-input {
  background: #f7f7f7;
  border-radius: 0;
  border-bottom: 1px solid #ccc;
  border-left: 0;
  border-top: 0;
  border-right: 0;
  flex: 1;
  margin: 10px 0;
  padding: 12px;
  width: 100%;
  box-sizing: border-box;
  color: #757575;
}

.form__buttons {
  display: flex;
  justify-content: space-between;
}

.form__button {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 10px 1px;
  padding: 15px;
  width: 100%;
  box-sizing: border-box;
  color: #757575;
  background: rgba(0, 0, 0, 0.15);
  text-align: center;
  font-weight: 600;
  font-size: 1em;
  cursor: pointer;
  max-height: 45px;
  border: none;
}
.form__button:hover {
  background: #a2a2a2;
  color: #fff;
}

.form__button-secondary {
  color: #949494;
  padding: 10px 3px;
  display: flex;
  justify-content: center;
  cursor: pointer;
}
.form__button-secondary:hover {
  background: #a2a2a2;
  color: #fff;
}

.bottom {
  display: flex;
  align-items: center;
}
.bottom li {
  flex: 1;
  text-align: center;
  display: flex;
}
.bottom li a {
  color: #949494;
  flex-basis: 100%;
  padding: 10px 3px;
}
.bottom li a:hover {
  color: #fff;
  background-color: #a2a2a2;
}

.list {
  display: flex;
  flex-flow: column;
  justify-content: flex-start;
  width: clamp(200px, 63%, 70%);
  margin: 10px auto;
}

.list__header {
  font-size: 2em;
  color: #5a5a5a;
  margin: 15px 0;
}

.list__item {
  color: #5a5a5a;
  border: 1px solid #eee;
  padding: 10px;
}
.list__item:hover {
  background-color: #e7e7e7;
}

:root {
  --color-main: #6ca3fe;
}

main {
  display: flex;
  flex: 1 0 auto;
  max-width: 1310px;
  margin: auto;
  padding: 0 10px;
  flex-flow: column;
}

.banner {
  display: flex;
  flex-flow: column;
  align-items: flex-start;
  min-height: 47vw;
}
.banner.gloves {
  background: url("../../pic/site-gloves.jpg") no-repeat;
  background-position: center right;
  background-size: cover;
}
.banner.boot-cover {
  background: url("../../pic/site-bootcover-824.jpg") no-repeat;
  background-position: center left;
  background-size: cover;
  align-items: flex-end;
}
.banner.endosirynge {
  background: url("../../pic/site-syringe-gradientt.jpg") no-repeat;
  background-position: center right;
  background-size: cover;
}
.banner .banner__container {
  background: rgba(0, 0, 0, 0.3);
}
.banner .banner__container.right {
  padding: 5% 10px 3% 17px;
  margin-left: 0;
  color: white;
}
@media (max-width: 600px) {
  .banner .banner__container.right {
    margin-left: 0;
  }
}
.banner .banner__container.left {
  padding: 5% 17px 3% 10px;
  margin-right: 0;
  color: white;
}
@media (max-width: 600px) {
  .banner .banner__container.left {
    margin-right: 0;
  }
}
@media (max-width: 900px) {
  .banner .banner__container {
    padding: 12% 40px 8% 40px;
  }
}
@media (max-width: 600px) {
  .banner .banner__container {
    padding: 8% 15px 6% 15px;
  }
}
@media (max-width: 300px) {
  .banner .banner__container {
    padding: 5% 20px 3% 20px;
  }
}
.banner .banner__text {
  align-items: flex-start;
  display: flex;
  flex-flow: column;
  justify-content: space-between;
  color: #2a2a2a;
  margin-bottom: 8%;
  margin-top: 3%;
  max-width: 35%;
}
@media (max-width: 1000px) {
  .banner .banner__text {
    max-width: 50%;
  }
}
@media (max-width: 600px) {
  .banner .banner__text {
    max-width: 55%;
  }
}
@media (max-width: 300px) {
  .banner .banner__text {
    max-width: 85%;
  }
}
.banner h1 {
  font-size: xx-large;
  margin: 0 0 35px;
  letter-spacing: 3px;
}
@media (max-width: 600px) {
  .banner h1 {
    font-size: 22px;
    margin-bottom: 20px;
  }
}
@media (max-width: 300px) {
  .banner h1 {
    font-size: 18px;
    margin-bottom: 10px;
    letter-spacing: 1px;
  }
}
.banner p {
  margin: 0;
  line-height: 25px;
}
@media (max-width: 600px) {
  .banner p {
    font-size: 12px;
    line-height: 17px;
  }
}
@media (max-width: 300px) {
  .banner p {
    font-size: 10px;
    line-height: 10px;
  }
}
.banner .banner__CTA {
  margin-top: 10px;
  padding: 15px;
  text-transform: uppercase;
  cursor: pointer;
  border: 2px solid var(--color-main);
  background: rgba(0, 0, 0, 0.3);
  transition: background 500ms, color 300ms;
  color: #ffffff;
  font-weight: 600;
  letter-spacing: 2px;
}
.banner .banner__CTA:hover {
  background: var(--color-main);
}
.banner .banner__CTA.right {
  align-self: flex-end;
}
.banner .banner__CTA.left {
  align-self: flex-start;
}
@media (max-width: 600px) {
  .banner .banner__CTA {
    padding: 10px;
    font-size: 11px;
  }
}
@media (max-width: 300px) {
  .banner .banner__CTA {
    padding: 7px;
    font-size: 10px;
  }
}

.advantages {
  display: flex;
  justify-content: center;
}
.advantages .advantages__wrap {
  display: flex;
  flex-flow: column;
  align-items: center;
  width: 70%;
}
@media (max-width: 500px) {
  .advantages .advantages__wrap {
    width: 100%;
  }
}
.advantages .advantages__wrap .advantages__title {
  font-size: xx-large;
  font-weight: 600;
  margin: 65px 0;
  color: #2a2a2a;
}
.advantages .advantages__wrap .advantage__cards-wrap {
  display: grid;
  grid-template: 50% 50%/50% 50%;
  justify-items: flex-start;
}
.advantages .advantages__wrap .advantage__card {
  position: relative;
  display: flex;
  flex-flow: column;
  min-width: 50%;
  flex: 1 0;
  padding: 60px 40px 40px;
  box-sizing: border-box;
  font-size: 22px;
  font-weight: 600;
  height: 300px;
  color: #2a2a2a;
}
@media (max-width: 600px) {
  .advantages .advantages__wrap .advantage__card {
    padding: 17px;
  }
}
.advantages .advantages__wrap p {
  font-weight: initial;
  font-size: 1rem;
}
.advantages .delivery svg,
.advantages .assortiment svg,
.advantages .certificates svg,
.advantages .result svg {
  position: absolute;
  top: -54px;
  right: 14%;
  width: 169px;
  height: 208px;
  filter: blur(6px);
  stroke: var(--color-main);
  opacity: 0.3;
  z-index: -1;
}

.card {
  width: 50%;
  margin: 65px auto;
}
.card h1, .card h2, .card p {
  color: #4e4e4e;
}
.card p {
  font-size: 1rem;
  line-height: 2rem;
}
@media (max-width: 300px) {
  .card > h1, .card > h2 {
    font-size: 12px;
  }
  .card > p {
    font-size: 10px;
  }
}
@media (max-width: 600px) {
  .card {
    width: 95%;
    margin: 30px auto;
  }
}

.workflow {
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: center;
  font-size: xx-large;
  font-weight: 600;
  margin-bottom: 65px;
  color: #2a2a2a;
}
.workflow .workflow__title {
  margin: 50px 0;
}
.workflow .workflow__wrap {
  display: flex;
  flex-flow: column;
  align-items: flex-start;
  padding-left: 160px;
}
@media (max-width: 600px) {
  .workflow .workflow__wrap {
    padding: 72px;
  }
}
.workflow .workflow__wrap .step {
  font-size: large;
  font-weight: 400;
  padding: 20px 0;
  position: relative;
}
.workflow .workflow__wrap .step:after {
  content: "";
  position: absolute;
  left: -33px;
  top: 22px;
  width: 15px;
  height: 15px;
  border-radius: 10px;
  background-color: var(--color-main);
}
.workflow .workflow__wrap .step:before {
  content: "";
